ding: 6px 12px;\n\t}\n}\n\n// Input styling\n.SfxInput-root {\n\theight: auto !important;\n\tpadding: 0 !important;\n\t.SfxInput-Base {\n\t\tmargin: 0 !important;\n\t}\n}\n\n// Select styling\n.SfxSelect-root {\n\tpadding: 8px !important;\n}\n\n// Global buttons\n.SfxButton-root {\n\tmin-height: 44px !important;\n\tmargin: 0 !important;\n\tborder: transparent !important;\n\t&[color='error'] {\n\t\tcolor: white  !important;\n\t\tbackground-color: var(--color-error) !important;\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tborder-color: white !important;\n\t\t\tbackground-color: var(--color-error-hover) !important;\n\t\t}\n\t}\n\t&[color='primary'] {\n\t\tcolor: var(--color-primary-element-text)  !important;\n\t\tbackground-color: var(--color-primary-element) !important;\n\t\t&:hover,\n\t\t&:focus {\n\t\t\tbackground-color: var(--color-primary-element-hover) !important;\n\t\t}\n\t}\n}\n\n// Menu items\n.SfxMenuItem-root {\n\theight: 44px;\n\tpadding-left: 8px !important;\n\t// Center the menu entry icon and fix width\n\t> div {\n\t\tmargin-right: 0;\n\t\tpadding: 14px;\n\t\t// Minus the parent padding-left\n\t\tpadding: calc(14px - 8px);\n\t\tcursor: pointer;\n\t}\n\n\t// Disable jpeg saving (jpg is already here)\n\t&[value='jpeg'] {\n\t\tdisplay: none;\n\t}\n}\n\n// Modal\n.SfxModal-Container {\n\tmin-height: 300px;\n\tpadding: 22px;\n\n\t// Fill height\n\t.SfxModal-root,\n\t.SfxModalTitle-root {\n\t\tflex: 1 1 100%;\n\t\tjustify-content: center;\n\t\tcolor: var(--color-main-text);\n\t}\n\t.SfxModalTitle-Icon {\n\t\tmargin-bottom: 22px !important;\n\t\tbackground: none !important;\n\t\t// Fit EmptyContent styling\n\t\tsvg {\n\t\t\twidth: 64px;\n\t\t\theight: 64px;\n\t\t\topacity: .4;\n\t\t\t// Override all coloured icons\n\n\t\t\t--color-primary: var(--color-main-text);\n\t\t\t--color-error: var(--color-main-text);\n\t\t}\n\t}\n\t// Hide close icon (use cancel button)\n\t.SfxModalTitle-Close {\n\t\tdisplay: none !important;\n\t}\n\t// Modal actions buttons display\n\t.SfxModalActions-root {\n\t\tjustify-content: space-evenly !important;\n\t}\n}\n\n// Header buttons\n.FIE_topbar-center-options > button,\n.FIE_topbar-center-options > label {\n\tmargin-left: 6px !important;\n}\n\n// Tabs\n.FIE_tabs {\n\tpadding: 6px !important;\n\toverflow: hidden;\n\toverflow-y: auto;\n}\n\n.FIE_tab {\n\twidth: 80px !important;\n\theight: 80px !important;\n\tpadding: 8px;\n\tborder-radius: var(--border-radius-large) !important;\n\tsvg {\n\t\twidth: 16px;\n\t\theight: 16px;\n\t}\n\t&-label {\n\t\tmargin-top: 8px !important;\n\t\toverflow: hidden;\n\t\ttext-overflow: ellipsis;\n\t\tmax-width: 100%;\n\t\twhite-space: nowrap;\n\t\tdisplay: block !important;\n\t}\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: var(--color-background-hover) !important;\n\t}\n\n\t&[aria-selected=true] {\n\t\tcolor: var(--color-main-text);\n\t\tbackground-color: var(--color-background-dark);\n\t\tbox-shadow: 0 0 0 2px var(--color-primary-element);\n\t}\n}\n\n// Tools bar\n.FIE_tools-bar {\n\t&-wrapper {\n\t\tmax-height: max-content !important;\n\t}\n\n\t// Matching buttons tools\n\t& > div[class$='-tool-button'],\n\t& > div[class$='-tool'] {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tjustify-content: center;\n\t\tmin-width: 44px;\n\t\theight: 44px;\n\t\tpadding: 6px 16px;\n\t\tborder-radius: var(--border-radius-pill);\n\t}\n}\n\n// Crop preset select button\n.FIE_crop-presets-opener-button {\n\t// override default button width\n\tmin-width: 0 !important;\n\tpadding: 5px !important;\n\tpadding-left: 10px !important;\n\tborder: none !important;\n\tbackground-color: transparent !important;\n}\n\n// Force icon-only style\n.FIE_topbar-history-buttons button,\n.FIE_topbar-close-button,\n.FIE_resize-ratio-locker {\n\tborder: none !important;\n\tbackground-color: transparent !important;\n\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: var(--color-background-hover) !important;\n\t}\n\n\tsvg {\n\t\twidth: 16px;\n\t\theight: 16px;\n\t}\n}\n\n// Left top bar buttons\n.FIE_topbar-history-buttons button {\n\t&.FIE_topbar-reset-button {\n\t\t&::before {\n\t\t\tcontent: attr(title);\n\t\t\tfont-weight: normal;\n\t\t}\n\t\tsvg {\n\t\t\tdisplay: none;\n\t\t}\n\t}\n}\n\n// Save button fixes\n.FIE_topbar-save-wrapper {\n\twidth: auto !important;\n}\n\n.FIE_topbar-save-button {\n\tcolor: var(--color-primary-text) !important;\n\tborder: none !important;\n\tbackground-color: var(--color-primary-element) !important;\n\t&:hover,\n\t&:focus {\n\t\tbackground-color: var(--color-primary-element-hover) !important;\n\t}\n}\n\n// Save Modal fixes\n.FIE_resize-tool-options {\n\t.FIE_resize-width-option,\n\t.FIE_resize-height-option {\n\t\tflex: 1 1;\n\t\tmin-width: 0;\n\t}\n}\n\n// Resize lock\n.FIE_resize-ratio-locker {\n\tmargin-right: 8px !important;\n\t// Icon is very thin\n\tsvg {\n\t\twidth: 20px;\n\t\theight: 20px;\n\t\tpath {\n\t\t\tstroke-width: 1;\n\t\t\tstroke: var(--color-main-text);\n\t\t\tfill: var(--color-main-text);\n\t\t}\n\t}\n}\n\n// Close editor button fixes\n.FIE_topbar-close-button {\n\tsvg path {\n\t\t// The path viewbox is weird and\n\t\t// not correct, this fixes it\n\t\ttransform: scale(1.6);\n\t}\n}\n\n// Canvas container\n.FIE_canvas-container {\n\tbackground-color: var(--color-main-background) !important;\n}\n\n// Loader\n.FIE_spinner::after,\n.FIE_spinner-label {\n\tdisplay: none !important;\n}\n\n.FIE_spinner-wrapper {\n\tbackground-color: transparent !important;\n}\n\n.FIE_spinner::before {\n\tposition: absolute;\n\tz-index: 2;\n\ttop: 50%;\n\tleft: 50%;\n\twidth: 28px;\n\theight: 28px;\n\tmargin: -16px 0 0 -16px;\n\tcontent: '';\n\t-webkit-transform-origin: center;\n\t-ms-transform-origin: center;\n\ttransform-origin: center;\n\t-webkit-animation: rotate .8s infinite linear;\n\tanimation: rotate .8s infinite linear;\n\tborder: 2px solid var(--color-loading-light);\n\tborder-top-color: var(--color-loading-dark);\n\tborder-radius: 100%;\n\n\tfilter: var(--background-invert-if-dark);\n}\n\n"],"sourceRoot":""}]);
